.smarttrix_form {
  max-width: 456px;
  width: 100%;
  padding: 34.5px 37px;
  box-sizing: border-box;
  background: #282828;
  border-radius: 8px;
}

.smarttrix_titleForm {
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: #FFFFFF;
  margin-bottom: 6px;
}

.smarttrix_descriptionForm {
  color: rgba(255, 255, 255, 0.4);
  margin-bottom: 32px;
}

.smarttrix_rememberMe {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.smarttrix_buttonSubmit {
  margin-top: 40px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15.5px 0px;
  background: #1EBD23;
  border-radius: 12px;
  border: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
}

.smarttrix_moreOption {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  color: rgba(255, 255, 255, 0.5) !important;
  font-size: 14px !important;
  width: 100% !important;
  margin-bottom: 0px !important;
  white-space: nowrap !important;
}

.smarttrix_moreOption_link {
  font-weight: 600;
  color: #1EBD23;
}

.smarttrix_resendCode {
  position: absolute;
  right: 10px;
  bottom: 18px;
  width: 75px;
  text-align: right;
  font-size: 12px;
  line-height: 15px;
  color: #FFF066;
}

@media (max-width: 479px) {
  .smarttrix_form {
    background: transparent;
    padding: 34.5px 16px;
  }
}

@media (max-width: 350px) {
  .smarttrix_moreOption {
    display: block !important;
    text-align: center;
  }
}