.overview-page {
  /*  */
}

.title {
  margin-bottom: 42px;
}

.sort {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 11px;
}

.form {
  max-width: 382px;
  width: 100%;
}

.formLabel {
  margin-bottom: 10px;
  width: 100%;
}

.formInput {
  margin-bottom: 20px;
  width: 100%;
  height: 48px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  border: 0px;
  padding: 15px 25px;
  box-sizing: border-box;
}

.formSubmit {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1EBD23;
  border-radius: 12px;
}